import React, { ReactNode } from 'react';

import { IconChevronLeft, IconChevronRight } from '../../../src/icons';

import styles from './NavHeader.module.scss';
import commonStyles from '../styles/common.scss';

interface NavHeaderProps {
  /** Дочерние элементы */
  children?: ReactNode;

  /** Обработчик нажатия на кнопку "Назад" */
  onPrev?: VoidFunction;

  /** Обработчик нажатия на кнопку "Вперёд" */
  onNext?: VoidFunction;
}

/** Навигационная панель */
export function NavHeader({
  children,
  onPrev,
  onNext
}: NavHeaderProps): JSX.Element {
  const buttonStyles = `${ commonStyles.button } ${ styles.button }`;

  const prev = onPrev ? (
    <button
      type="button"
      className={ buttonStyles }
      onClick={ onPrev }
      tabIndex={ -1 }
    >
      <IconChevronLeft />
    </button>
  ) : (
    <div className={ styles.button } />
  );

  const next = onNext ? (
    <button
      type="button"
      className={ buttonStyles }
      onClick={ onNext }
      tabIndex={ -1 }
    >
      <IconChevronRight />
    </button>
  ) : (
    <div className={ styles.button } />
  );

  return (
    <div className={ styles.container }>
      { prev }
      <div className={ styles.content }>
        { children }
      </div>
      { next }
    </div>
  );
}
